<template>
    <div class="container">
        <h5 class="sidebar-heading d-flex justify-content-center align-items-center  text-center alert alert-dark">
            <span class="titModu">Actualización de tareas</span>
        </h5>
        <div class="row justify-content-md-center">
            <div class="col-md-9 offset-md-1">
                <div class="card">
                    <h5 class="card-header">Tareas</h5>
                    <div class="card-body">
                        <form v-on:submit.prevent="enviarFormulario()">
                            <div class="form-group">
                                <select v-if="tableros && tableros.length" v-on:change="cambioTablero()" id="tablero" class="btn btn-secondary col-lg-8" required>
                                    <option selected disabled>Seleccionar el Tablero...</option>
                                    <option v-for="tablero of tableros" v-bind:value="tablero.TableroId">{{tablero.TableroNombre}}</option>
                                </select>
                            </div>
                            <div class="form-group">
                                <select v-if="listaCartas && listaCartas.length" v-on:change="cambioCarta()" id="carta" class="btn btn-secondary col-lg-8" required>
                                    <option selected disabled>Tarea...</option>
                                    <option v-for="carta of listaCartas" v-bind:value="carta[0]">{{carta[0]}}</option>
                                </select>
                            </div>
                            <div class="form-group">
                                <input type="number" v-model="porcentaje" min="0" max="100" class="form-control mx-auto col-lg-8" id="porcentaje" placeholder="Porcentaje" required>
                                <!-- <input type="text" min="0" max="100" class="form-control mx-auto col-lg-8" id="estado" v-bind:value="estadoCarta" required> -->
                            </div>
                            <div class="form-group">
                                <div class="progress mx-auto col-lg-8">
                                    <div class="progress col-lg-12">
                                        <div class="progress-bar progress-bar-striped" v-bind:style="{ width: porcentaje+'%'}">{{porcentaje}}%</div>
                                    </div>
                                </div>
                            </div>
                            <div class="form-group">
                                <button type="submit" class="btn btn-primary">Actualizar</button>
                            </div>
                        </form>
                    </div>
                    <div class="card-footer text-muted">
                        Semillero de innovación geográfica
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';///importar la dependencia de axios
export default {
    name: 'ActualizacionTareas',
    props: {
        msg: String
    },
    data() {
        return {
            errors: [],//arreglo de errores axios
            tableros: [],//arreglo de tableros del servicio
            idTablero:'',//id del tablero elegido
            nombreslistas: [],//nombres de las listas
            nombreCarta: '',//nombre de la carta elegida
            listaCartas: [],//lista de cartas con el id de su tablero
            porcentaje: 0,
            estadoCarta: '',//nombre de la lista donde esta la carta
            tareasReportadas: [], //tareas que si tienen reporte
            idTarjetaSeleccionada: '',//id de la tarjeta si existe en el api tareas
            porcentajeTarjetaSeleccionada: '', // porcentaje de la tarea si esta registrada en el api
        }
    },
    created(){
        /*______________________________________________________________________________
        _______________________________________________________________________________-*/
        axios.get('https://cors-anywhere.herokuapp.com/http://geoapps.esri.co/json/pruebaJson.json')
        // axios.get('geoapps.esri.co/json/pruebaJson.json')
        .then(response => {
            // JSON responses are automatically parsed.
            this.tableros = response.data
            // console.log(this.tableros);
        })
        .catch(e => {
            this.errors.push(e)
        })
        // axios para tableros

        /*______________________________________________________________________________
        _______________________________________________________________________________-*/
        axios.get('https://cors-anywhere.herokuapp.com/http://geoapps.esri.co:81/api/tarea/')
        // axios.get('geoapps.esri.co/json/pruebaJson.json')
        .then(response => {
            // JSON responses are automatically parsed.
            this.tareasReportadas = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
        // axios para tableros
    },
     methods:{
        //obtiene el correo del usuario cuando se cambia la opción del select
        getCorreoUsuario: function(){
            var selectUsuario = document.getElementById("usuario").value;
            this.idPersona = selectUsuario;
            console.log(this.idPersona);
            //vacia el arreglo para poder agregar los reportes de la persona al arreglo
            this.reportesPersona = [];
        },
        //obtiene el id del tablero cuando se cambia la opción del select
        cambioTablero: function(){
            var selectTablero = document.getElementById("tablero").value;
            this.idTablero = selectTablero;
            // console.log(this.idTablero);
            //vacia el arreglo para poder agregar las cartas del tablero
            this.listaCartas.length = 0;
            var indexTab = this.recorrerTableros(this.idTablero);
            var indexListas = this.recorrerListas(indexTab);
        },
        //recorre los tableros y retorna el indice del tablero que coincide con el id del tablero elegido
        recorrerTableros: function(idTab){
            for (var i = 0; i < this.tableros.length; i++) {
                if(this.tableros[i].TableroId === idTab){
                    return i;
                }
            }
        },
        //recorre las listas dentro del tablero que entra como indice
        recorrerListas: function(i){ 
        for (var j = 0; j < this.tableros[i].Lista.length; j++) {
                this.nombreslistas.push(this.tableros[i].Lista[j].NombreLista);
                this.recorrerCartas(i,j);
            }
        },
        //recorre todas las cartas que aparecen en ese tablero
        recorrerCartas: function(i,j){
            for (var k = 0; k < this.tableros[i].Lista[j].cartas.length; k++) {
                var nombre = this.tableros[i].Lista[j].cartas[k];
                var lista = this.tableros[i].Lista[j].NombreLista; //guarda el nombre de la lista en el tablero
                // var lista = j; guarda el indice de la lista en el tablero
                var cartaObj = [nombre, lista];
                this.listaCartas.push(cartaObj);
            }
        },
        // metodo para obtener el nombre de la carta que se eligio
        cambioCarta: function(){
            var select = document.getElementById("carta").value;
            for (let index = 0; index < this.listaCartas.length; index++) {
                const element = this.listaCartas[index];
                if(element[0] == select){
                    this.estadoCarta = element[1];//coloca el estado de la carta en la variable para usarla
                    //al final al momento de meterla en el servicio, esta es la lista en la que esta la tarjeta
                    if(this.tareaReportada(element[0])){
                        this.porcentaje = this.porcentajeTarjetaSeleccionada; //si existe ahi coloca el porcentaje que va
                    }else{
                        this.porcentaje = 0; //si no existe ahi el porcentaje es 0
                    }
                }
            }
        },
        // comprueba si la tarea esta en el api tarea
        tareaReportada: function(nombreTarea){
            for (let index = 0; index < this.tareasReportadas.length; index++) {
                const element = this.tareasReportadas[index];
                if(element.tarjeta == nombreTarea){
                    this.idTarjetaSeleccionada = element.id_tarea;//tenemos el id si la tarea existe en el api tarea
                    this.porcentajeTarjetaSeleccionada = element.porcentaje_avance;  //tenemos el procentaje si esta existe en el api tarea
                    return true;
                }else{
                    return false;
                }
            }
        },
        /////metodo para enviar el formulario y hacer el post
        enviarFormulario: function() {
            //document.getElementById('formularioReporeHoras').addEventListener('submit', enviar);
            var headers = {
                "Content-Type": "application/json",
                "Allow": "*",
            };
            var tablero = document.getElementById("tablero").value;
            var carta = document.getElementById("carta").value;
            var porc = document.getElementById("porcentaje").value;
            var estadoTarea = this.estadoCarta;

            //compila todos los datos que se usan en el registro dentro de una variable
            var data = {
                tarjeta: carta,
                tablero: tablero,
                estado_tarjeta: estadoTarea,
                porcentaje_avance: porc,
            };
            var test= JSON.stringify(data);
            console.log(test);
            // verifica si existe la tarea segun el nombre y la actualiza
            // si no esta reportada entonces se crea la tarea en el api tarea
            if(this.tareaReportada(carta)){
                //put se usa para actualizar y se pasa el primary key en la url como parametro al final un /
                axios.put('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/tarea/'+carta+'/', test, {headers: headers})
                // axios.post('geoapps.esri.co:81/api/reporte/',  test, {headers: headers})
                .then(function (response) {
                    console.log("Actualizacion de tarjeta");
                    alert("Actualizacion de reporte agregado");
                })
                .catch(function (error) {
                    console.log(error);
                });
            }else{
                axios.post('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/tarea/', test, {headers: headers})
                // axios.post('geoapps.esri.co:81/api/reporte/',  test, {headers: headers})
                .then(function (response) {
                    console.log("Nueva tarjeta en el api");
                    alert("Nuevo reporte agregado");
                })
                .catch(function (error) {
                    console.log(error);
                });
            } 
        }
     }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h5{
    margin-top: 1em;
    background-color: #0c0c0c00;
    color: white;
}
.title{
    text-align: center;
    margin-top: 2em;
    margin-bottom: 1em;
}

.progress{
    padding: 0;
}
</style>
