import Vue from 'vue'
import Router from 'vue-router'
import Inicio from './components/Inicio.vue'
import Integrantes from './components/Integrantes.vue'
import Perfil from './components/Perfil.vue'
import Registro from './components/Registro.vue'
import RegistroHoras from './components/RegistroHoras.vue'
import Login from './components/Login.vue'
import ProyectosSinReporte from './components/ProyectosSinReporte.vue'
import ActualizacionTareas from './components/ActualizacionTareas.vue'
import VistaAdmin from './components/VistaAdmin.vue'

Vue.use(Router);

export default new Router({
  routes:[
    {
      path: '/Inicio',
      name: 'Inicio',
      component: Inicio
    },
    {
      path: '/Integrantes',
      name: 'Integrantes',
      component: Integrantes
    },
    {
      path: '/Perfil',
      name: 'Perfil',
      component: Perfil
    },
    {
      path: '/Registro',
      name: 'Registro',
      component: Registro
    },
    {
      path: '/RegistroHoras',
      name: 'RegistroHoras',
      component: RegistroHoras
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/ProyectosSinReporte',
      name: 'ProyectosSinReporte',
      component: ProyectosSinReporte
    },
    {
      path: '/ActualizacionTareas',
      name: 'ActualizacionTareas',
      component: ActualizacionTareas
    },
    {
      path: '/VistaAdmin',
      name: 'VistaAdmin',
      component: VistaAdmin
    }
  ]
})
