<template>
    <div class="container">
        <h5 class="sidebar-heading d-flex justify-content-center align-items-center  text-center alert alert-dark">
            <span class="titModu">Integrantes Semillero</span>
        </h5>

        <table class="table">
            <thead class="thead-dark">
                <tr>
                    <th scope="col">Foto</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Horas(H)</th>
                    <th scope="col">Estado</th>
                    <th scope="col">Editar</th>
                </tr>
            </thead>
            <tbody v-if="personas && personas.length">
                <tr v-for="persona of personas">
                <th scope="row"><img class="fotoPerfil" src="http://geoapps.esri.co/appreportehorasresources/perfil.png" alt="foto perfil"></th>
                <td><a href="#"><router-link to="/Perfil">{{persona.nombres}} {{persona.apellidos}}</router-link></a></td>
                <td>{{persona.correo}}</td>
                <td>{{cambioEstado(persona.estado)}}</td>
                <td><button type="button" title="editar">
                    <img class="botonEditar" src="http://geoapps.esri.co/appreportehorasresources/editar.png"></button></td>
                </tr>
            </tbody>
        </table>
        <ul v-if="errors && errors.length">
            <li v-for="error of errors">
                {{error.message}}
            </li>
        </ul>


        <!-- <div class="container">
            <ul v-if="posts && posts.length">
                <li v-for="post of posts">
                    <p><strong>{{post.nombres}}</strong></p>
                    <p>{{post.correo}}</p>
                    <p>{{cambioEstado(post.estado)}}</p>
                </li>
            </ul>

            <ul v-if="errors && errors.length">
                <li v-for="error of errors">
                    {{error.message}}
                </li>
            </ul>
        </div> -->

    </div>
</template>

<script>
import axios from 'axios';///importar la dependencia de axios
import Perfil from './Perfil.vue';
export default {
    name: 'Integrantes',
    props: {
        msg: String
    },
    data() {
        return {
            personas: [],
            errors: []
        }
    },

    methods:{
        cambioEstado: function(estadoBoolean){
            return estadoBoolean != true ? 'Activo':'Inactivo';
        }
    },
    // Fetches posts when the component is created.
    created() {
        axios.get('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/persona/')
        // axios.get('geoapps.esri.co:81/api/persona/')
        .then(response => {
            // JSON responses are automatically parsed.
            this.personas = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })

        // async / await version (created() becomes async created())
        //
        // try {
        //   const response = await axios.get(`http://jsonplaceholder.typicode.com/posts`)
        //   this.posts = response.data
        // } catch (e) {
        //   this.errors.push(e)
        // }
    }
}
</script>

