<template>
    <div class="container">
        <h5 class="sidebar-heading d-flex justify-content-center align-items-center  text-center alert alert-dark">
            <span class="titModu">Registro de Integrantes</span>
        </h5>
        <form id="formularioEnvio" v-on:submit.prevent="enviarFormulario()">
            <div class="row">
                <div class="col-2">
                    <img class="card-img-top my-3" src="http://geoapps.esri.co/appreportehorasresources/perfil.png" alt="Foto de perfil">
                    <div class="form-group">
                      <label for="url">Imagen de perfil</label>
                      <input type="text" class="form-control" id="url" placeholder="Url imagen">
                    </div>
                </div>
                <div class="col-5">
                    <div class="form-group">
                      <label for="nombre">Nombres</label>
                      <input type="text" class="form-control" id="nombres" placeholder="Nombres" required>
                    </div>
                    <div class="form-group">
                      <label for="apellidos">Apellidos</label>
                      <input type="text" class="form-control" id="apellidos" placeholder="Apellidos" required>
                    </div>
                    <div class="form-group">
                      <label for="edad">Edad</label>
                      <input type="text" class="form-control" id="edad" placeholder="Edad">
                    </div>
                    <div class="form-group">
                      <label for="fechaNacimiento">Fecha Nacimiento</label>
                      <input type="date" class="form-control" id="fechaNacimiento" placeholder="MM/DD/AAAA" required>
                    </div>
                    <div class="form-group">
                      <label for="email">Correo Electronico</label>
                      <input type="email" class="form-control" id="email" placeholder="Correo electronico" required>
                    </div>
                    <div class="form-group">
                      <label for="docId">Documento Identidad</label>
                      <input type="number" class="form-control" id="docId" placeholder="Número de documento" required>
                    </div>
                </div>
                <div class="col-5">
                    <div class="form-group">
                      <label for="universidad">Universidad</label>
                      <input type="text" class="form-control" id="universidad" placeholder="Universidad" required>
                    </div>
                    <div class="form-group">
                      <label for="telefono">Telefono</label>
                      <input type="number" class="form-control" id="telefono" placeholder="Telefono" required>
                    </div>
                    <div class="form-group">
                      <label for="estado">Estado</label>
                      <select class="form-control" id="estado" required>
                          <option selected disabled>Estado</option>
                          <option value="Activo" >Activo</option>
                          <option value="No Activo">No activo</option>
                      </select>
                    </div>
                    <div class="form-group">
                      <label for="estado">Talla</label>
                      <select class="form-control" id="talla" required>
                          <option selected disabled>Talla de camiseta</option>
                          <option value="XS">XS</option>
                          <option value="S">S</option>
                          <option value="M">M</option>
                          <option value="L">L</option>
                          <option value="XL">XL</option>
                      </select>
                    </div>
                    <div class="form-group">
                      <label for="estado">Rol</label>
                      <select class="form-control" id="rol" required>
                          <option selected disabled>Rol</option>
                          <option value="Desarrollador">Desarrollador</option>
                          <option value="Tematico">Temático</option>
                      </select>
                    </div>
                    <div class="form-group">
                      <label for="rol">Fecha tentativa de grado</label>
                      <input type="date" class="form-control" id="fechaGrado" placeholder="Fecha Grado">
                      <input type="text" class="form-control" id="expGrado" placeholder="Observaciones">
                    </div>
                    <div class="form-group">
                      <button type="submit" class="btn btn-primary">Guardar</button>
                    </div>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import axios from 'axios';///importar la dependencia de axios
export default {
  name: 'Registro',
  props: {
    msg: String
  },
  methods:{
    enviarFormulario: function() {

      // document.getElementById('formularioEnvio').addEventListener('submit', enviar);
      var headers = {
              "Content-Type": "application/json",
              "Allow": "*",
      };

    // var urlImagen = document.getElementById("url").value;
    var nombres = document.getElementById("nombres").value;
    var apellidos = document.getElementById("apellidos").value;
    var edad = document.getElementById("edad").value;
    var fNto = document.getElementById("fechaNacimiento").value;
    var correo = document.getElementById("email").value;
    var documentoIdentidad = document.getElementById("docId").value;
    var universidad = document.getElementById("universidad").value;
    var telefono = document.getElementById("telefono").value;
    var estado = document.getElementById('estado').value;
    var tallaCamiseta = document.getElementById("talla").value;
    var rol = document.getElementById("rol").value;
    var fechaGrado = document.getElementById("fechaGrado").value;
    var expGrado = document.getElementById("expGrado").value;
    

      var estadoNum = true;
      if(estado === 'Activo'){
        estadoNum = true;
      }
      else{
        estadoNum = false;
      }


      var data = {
          // foto:urlImagen,
          nombres: nombres,
          apellidos: apellidos,
          fecha_nacimiento:fNto,
          correo: correo,
          documento_identidad: documentoIdentidad,
          universidad: universidad,
          estado_persona:estadoNum,
          talla_camiseta: tallaCamiseta,
          rol_persona: rol,
          fecha_ten_grado: fechaGrado,
          exp_fecha: expGrado
          };
          var test= JSON.stringify(data);
          // console.log(test);

        // console.log(data);
          var test1 = new FormData();
        // console.log("test1");

        axios.post('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/persona/',  test, {headers: headers})
        // axios.post('geoapps.esri.co:81/api/persona/',  test, {headers: headers})
        .then(function(response) {
          // resultElement.innerHTML = generateSuccessHTMLOutput(response);
              console.log(response);
              alert("Persona Registrada");
        }).catch(function(error) {
            // resultElement.innerHTML = generateErrorHTMLOutput(error);
              console.log(error);
        })

      }

  }
}
</script>

