<template>
    <div class="container">
        <h5 class="sidebar-heading d-flex justify-content-center align-items-center  text-center alert alert-dark">
            <span class="titModu">Reporte de horas</span>
        </h5>
        <form id="formularioReporeHoras" v-on:submit.prevent="enviarFormulario()">
            <div class="form-group">
                <label for="usuarios">Usuario:</label>
                <select v-if="personas && personas.length" class="btn btn-secondary selectUsuario" id="usuario" v-on:change="getCorreoUsuario()" >
                    <option selected disabled>Seleccionar persona..</option>
                    <option v-for="persona of personas" v-bind:value="persona.correo" v-model="reportesPersona">{{persona.nombres +' '+ persona.apellidos}}</option>
                </select>
            </div>
            <!-- primera fila para el registro de la hora -->
            <div class="row">
                <div id="caja1">
                    <div class="row">
                        <div class="col-2">
                            <button type="button" class="btn btn-primary col-lg-12" disabled="true">Horas</button>
                        </div>
                        <div class="col-3">
                            <button type="button" class="btn btn-primary col-lg-12" disabled="true">Fecha</button>
                        </div>
                        <div class="col-7">
                            <button type="button" class="btn btn-primary col-lg-12" disabled="true" >Descripción</button>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-2">
                            <input class="form-control col-lg-12" type="number" min="0" max="1000" id="horas" step="1"/>
                        </div>
                        <div class="col-3">
                            <input type="date" class="form-control" name="" id="fechareporte" />
                        </div>
                        <div class="col-7">
                            <input type="text" class="form-control" name="descripcion" id="observacion" placeholder="observacion de la actividad...">
                        </div>
                    </div>
                    <hr>
                    <div class="row">
                        <div class="col-3">
                            <button type="button" class="btn btn-primary col-lg-12"  disabled="true">Tablero</button>
                        </div>
                        <div class="col-9">
                            <button type="button" class="btn btn-primary col-lg-12" disabled="true">Tarea</button>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-3">
                            <select v-if="tableros && tableros.length" v-on:change="cambioTablero()" class="btn btn-secondary col-lg-12" id="tablero">
                                <option selected disabled>Seleccionar tablero</option>
                                <option v-for="tablero of tableros" v-bind:value="tablero.TableroId">{{tablero.TableroNombre}}</option>
                            </select>
                        </div>
                        <div class="col-9">
                            <select v-if="cartas && cartas.length" v-on:change="cambioCartas()" class="btn btn-secondary col-lg-12" id="carta">
                                <option selected disabled>Seleccionar</option>
                                <option v-for="carta of cartas" v-bind:value="carta">{{carta}}</option>
                            </select>
                        </div>
                    </div>
                    <button type="submit" class="btn btn-primary boton"> Guardar </button>
                </div>
            </div>
        </form>
        <!-- segunda fila para filtro de hora -->
        <div class="row">
            <div class="innercaja">
                <h5 class="card-header">Consulta</h5>
                <form id="formFechas" v-on:submit.prevent="getReportes()">
                    <div class="row rows">
                        <div class="col-6">
                            <h6>Fecha inicio:</h6>
                            <input type="date" class="form-control space" name="f-ini" id="f-ini">
                        </div>
                        <div class="col-6">
                            <h6>Fecha fin:</h6>
                            <input type="date" class="form-control space" name="f-fin" id="f-fin">
                        </div>
                    </div>
                    <button type="submit" class="btn btn-primary rows"> Buscar </button>
                </form>
            </div>
        </div>
        <!-- tercera fila para el listado de tareas registradas -->
        <div class="row">
            <div id="caja2">
                <table class="table table-bordered">
                    <thead class="thead-dark">
                        <tr class="table-secondary" style="text-align: center">
                            <th scope="col">Fecha</th>
                            <th scope="col">Tablero</th>
                            <th scope="col">Tarea</th>
                            <th scope="col">Horas</th>
                            <th scope="col">Descripción</th>
                            <th scope="col">Eliminar</th>
                        </tr>
                    </thead>
                    <tbody v-if="reportesPersona && reportesPersona.length" v-model="reportesPersona">
                        <tr v-for="reporte of reportesPersona">
                            <td id="fechaRep">{{reporte.fecha_de_reporte}}</td>
                            <td id="tableroRep">{{traducirTablero(reporte.tablero)}}</td>
                            <td id="cartaRep">{{reporte.tarjeta}}</td>
                            <th id="horasRep" scope="row">{{reporte.horas_reportadas}}</th>
                            <td id="descripcionRep">{{reporte.observacion}}</td>
                            <th><a v-on:click="borrarReporte(reporte)" href="#"><img class="icono2" src="http://contraloriadelmagdalena.gov.co/contratacion/imagenes/botones/eliminar.png"></a></th>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';///importar la dependencia de axios
export default {
    name: 'RegistroHoras',
    props: {
        msg: String
    },
    data() {
        return {
            personas: [],//arreglo de personas del servicio
            errors: [],//arreglo de errores axios
            idPersona:'',//id de la persona, en este caso el correo
            tableros: [],//arreglo de tableros del servicio
            idTablero:'',//id del tablero elegido
            cartas: [],//lista de cartas presentes en cada tablero, se vacia/llena cada ve que se cambia de tablero
            nombreslistas: [],//nombres de las listas
            nombreCarta: '',//nombre de la carta elegida
            listaCartas: [],//lista de cartas con el id de su tablero
            lista: '',//
            reportesPersona: [],//arreglo de los reportes que hace la persona
        }
    },
    created() {
        /*______________________________________________________________________________
        _______________________________________________________________________________-*/
        axios.get('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/persona/?format=json')
        // axios.get('geoapps.esri.co:81/api/persona/?format=json')
        .then(response => {
            // JSON responses are automatically parsed.
            this.personas = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
        // axios para get de personas

        /*______________________________________________________________________________
        _______________________________________________________________________________-*/
        axios.get('https://cors-anywhere.herokuapp.com/geoapps.esri.co/json/pruebaJson.json')
        // axios.get('geoapps.esri.co/json/pruebaJson.json')
        .then(response => {
            // JSON responses are automatically parsed.
            this.tableros = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
        // axios para tableros
        /*______________________________________________________________________________
        _______________________________________________________________________________-*/
        axios.get('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/reporte/')
        // axios.get('geoapps.esri.co/json/pruebaJson.json')
        .then(response => {
            // JSON responses are automatically parsed.
            this.tareasReportadas = response.data
            console.log(this.tareasReportadas);
        })
        .catch(e => {
            this.errors.push(e)
        })
        // axios para tableros
    },
    methods:{
        //obtiene el correo del usuario cuando se cambia la opción del select
        getCorreoUsuario: function(){
            var selectUsuario = document.getElementById("usuario").value;
            this.idPersona = selectUsuario;
            console.log(this.idPersona);
            //vacia el arreglo para poder agregar los reportes de la persona al arreglo
            this.reportesPersona = [];
        },
        //obtiene el id del tablero cuando se cambia la opción del select
        cambioTablero: function(){
            var selectTablero = document.getElementById("tablero").value;
            this.idTablero = selectTablero;
            // console.log(this.idTablero);
            //vacia el arreglo para poder agregar las cartas del tablero
            this.cartas.length = 0;
            var indexTab = this.recorrerTableros(this.idTablero);
            var indexListas = this.recorrerListas(indexTab);
        },

        //recorre los tableros y retorna el indice del tablero que coincide con el id del tablero elegido
        recorrerTableros: function(idTab){
            for (var i = 0; i < this.tableros.length; i++) {
                if(this.tableros[i].TableroId === idTab){
                    return i;
                }
            }
        },
        //recorre las listas dentro del tablero que entra como indice
        recorrerListas: function(i){ 
        for (var j = 0; j < this.tableros[i].Lista.length; j++) {
                this.nombreslistas.push(this.tableros[i].Lista[j]);
                this.recorrerCartas(i,j);
            }
        },

        //recorre todas las cartas que aparecen en ese tablero
        recorrerCartas: function(i,j){
            for (var k = 0; k < this.tableros[i].Lista[j].cartas.length; k++) {
                this.cartas.push(this.tableros[i].Lista[j].cartas[k]);
                var nombre = this.tableros[i].Lista[j].cartas[k];
                var lista = j;
                var cartaObj = [nombre, lista];
                this.listaCartas.push(cartaObj);
            }
        },
        traducirTablero: function(idTablero){
            for (let index = 0; index < this.tableros.length; index++) {
                if(idTablero == this.tableros[index].TableroId){
                    return this.tableros[index].TableroNombre;
                }                
            }
        },
        // metodo para obtener el nombre de la carta que se eligio
        cambioCartas: function(){
            var selectCartas = document.getElementById("carta").value;
            this.nombreCarta = selectCartas;
            // console.log(this.nombreCarta);
            this.listaCartasR(this.nombreCarta);
            // console.log(this.nombreslistas[this.lista].NombreLista);
        },
        // recorrer la lista de cartas 
        listaCartasR: function(cartasR){
            for (var i = 0; i < this.listaCartas.length; i++) {
                if(cartasR===this.listaCartas[i][0]){
                this.lista = this.listaCartas[i][1];
                // console.log(this.lista);
                }
            }
        },
        //funcion para comprobar si la tarea esta registrada
        tareaReportada: function(nombreTarea){
            for (let index = 0; index < this.tareasReportadas.length; index++) {
                const element = this.tareasReportadas[index];
                if(element.tarjeta == nombreTarea){
                    return true;
                }else{
                    return false;
                }
            }
        },
        //obtener actividades reportadas por usuario
        getReportes: function(){
            var fini = document.getElementById("f-ini").value;
            var ffin = document.getElementById("f-fin").value;
            axios.get('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/reporte/?'+'fechai='+fini+'&fechaf='+ffin+'&correo='+this.idPersona)
            // axios.get('geoapps.esri.co:81/api/reporte/?'+'fechai='+fini+'&fechaf='+ffin+'&correo='+this.idPersona)
            .then(response => {
                this.reportesPersona = response.data;
                if(this.reportesPersona.length == 0){
                    alert("No hay reportes en este periodo: "+fini+' || '+ffin);
                }
            })
            .catch(e => {
                this.errors.push(e)
            })
            // axios para get de reportes
        },
        borrarReporte: function(reporte){
            var headers = {
                "Content-Type": "application/json",
                "Allow": "*",
            };
            var id = reporte.id_Reporte;
            //Ingresamos un mensaje a mostrar
            var mensaje = confirm("¿Desea borrar el reporte?");
            //Detectamos si el usuario acepto el mensaje
            if (mensaje) {
                /*______________________________________________________________________________
                _______________________________________________________________________________-*/
                axios.delete('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/reporte/'+id+'/', {headers: headers})
                // axios.get('geoapps.esri.co/json/pruebaJson.json')
                .then(response => {
                    // JSON responses are automatically parsed.
                    console.log(response);
                    alert("Reporte borrado");
                })
                .catch(e => {
                    this.errors.push(e);
                })
            }
        },
        /////metodo para enviar el formulario y hacer el post
        enviarFormulario: function() {
            //document.getElementById('formularioReporeHoras').addEventListener('submit', enviar);
            var headers = {
                "Content-Type": "application/json",
                "Allow": "*",
            };
            var usuario = document.getElementById("usuario").value;
            var horas = document.getElementById("horas").value;
            var fechaRep = document.getElementById("fechareporte").value;
            var tablero = document.getElementById("tablero").value;
            var carta = document.getElementById("carta").value;
            var observacion = document.getElementById("observacion").value;
            var x = String(horas + ".0");
            //compila todos los datos que se usan en el registro dentro de una variable
            var data = {
                correo: usuario,
                horas_reportadas: horas,
                fecha_de_reporte: fechaRep,
                tablero: tablero,
                tarjeta: carta,
                observacion: observacion,
                estado: this.nombreslistas[this.lista].NombreLista,
            };
            var test= JSON.stringify(data);
            // console.log(test);
            axios.post('https://cors-anywhere.herokuapp.com/geoapps.esri.co:81/api/reporte/',  test, {headers: headers})
            // axios.post('geoapps.esri.co:81/api/reporte/',  test, {headers: headers})
            .then(function (response) {
                console.log("Reporte agregado");
                alert("Reporte agregado");
            })
            .catch(function (error) {
                console.log(error);
            });
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h5 {
  margin-top: 1em;
  background-color: #0c0c0c00;
    color: white;
}
.selectUsuario {
  width: 75%;
}

#caja1 {
  border-radius: 4px;
  border: 2px solid darkgray;
  padding: 2em 2em 2em 2em;
  width: 90%;
  margin: 0px auto;
}

#caja2 {
  border-radius: 4px;
  border: 2px solid gray;
  padding: 1em 1em 1em 1em;
  width: 90%;
  margin: 0px auto;
}

.innercaja {
  border-radius: 10px;
  border: 2px solid lightgray;
  max-width: 500px;
  padding: 0px;
  margin: 0px auto;
}

.row {
  padding-top: 1em;
}

.space {
  margin-bottom: 1em;
}

.rows {
  margin: 1em;
}

.boton {
  margin-top: 1em;
}

.icono1 {
  width: 40%;
}
.icono2 {
  width: 3em;
}

.card-header {
  margin: 0;
}
</style>
