<template>
    <div class="container">
        <h5 class="sidebar-heading d-flex justify-content-center align-items-center  text-center alert alert-dark">
            <span class="titModu">Tableros / tarjetas</span>
            <br>
            <button id="botonReporte" v-on:click="createChart()" class="titModu btn">Generar Reporte</button>
        </h5>
        <!-- <grafica></grafica> -->
        <div class="row justify-content-center">
          <div class="col col-lg-12">
            <canvas id="myState" v-on:click='detectarTablero' v-model="graficaBarras">El navegador no soporta CANVAS</canvas>
          </div>
          <div class="col col-lg-12">
            <b-carousel id="carousel1"
                        style="text-shadow: 1px 1px 2px #333;"
                        controls
                        indicators
                        background="#cecccc"
                        :interval="4000"
                        img-width="1024"
                        img-height="500"
                        v-model="slide"
                        @sliding-start="onSlideStart"
                        @sliding-end="onSlideEnd"
            >

              <!-- Text slides with image -->
              <b-carousel-slide img-blank>
                <canvas id="myPriority">El navegador no soporta CANVAS</canvas>
              </b-carousel-slide>

              <!-- Slides with custom text -->
              <b-carousel-slide img-blank>
                <div class="row">
                      <div class="col col-lg-12">
                        <h3><strong>Estado de las tareas</strong></h3>
                      </div>
                      <div class="col col-lg-12" v-if="todasListas && todasListas.length">
                        <!-- class="nav navbar-nav" -->
                        <ul class="list-inline" v-for="lista of todasListas">
                          <li class="list-inline-item itemlista" v-on:click="elementosLista(lista)">{{lista}}</li>
                        </ul>
                      </div>
                      <div class="col col-lg-12">
                        <table class="table  table-hover" style="overflow:auto;">
                          <thead>
                            <tr class="barras">
                              <th scope="col">Tarea</th>
                              <th scope="col">Estado</th>
                              <th scope="col">Porcentaje ejecutado (%)</th>
                              <th scope="col">Horas reportadas (Hr)</th>
                            </tr>
                          </thead>
                          <tbody v-if="cartasListadas && cartasListadas.length">
                            <tr v-for="carta of cartasListadas">
                              <td id="">{{carta}}</td>
                              <td id=""><button type="button" class="btn btn-primary espacio" disabled="true"> proceso
                                </button></td>
                              <td id="">
                                <div class="progress">
                                  <div class="progress-bar" role="progressbar" style="width: 25%;" aria-valuenow="25"
                                    aria-valuemin="0" aria-valuemax="100">25%</div>
                                </div>
                              </td>
                              <td id="">200</td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                  </div>
              </b-carousel-slide>
              <!-- <p class="mt-4">
                  Slide #: {{ slide }}<br>
                  Is Sliding: {{ sliding }}
              </p> -->
            </b-carousel>
          </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';///importar la dependencia de axios
import Chart from 'chart.js';
export default {
    name: 'VistaAdmin',
    props: {
        msg: String
    },
    data() {
        return {
            tableros: [],//arreglo de tableros del servicio
            longitudArregloTableros: '',//variable que guarda el largo de tableros
            colors: [],//arreglo de colores que se llena al inicio
            listaTablero: [],
            listaCartas: [],
            numeroTareas:[],
            errors: [],//arreglo de errores obtenidos al cargar el rest con axios
            slide: 0, //para el carousel
            sliding: null,//estado que comprueba si estan haciendo slide o no
            graficaDona:'',//variable que guarda referencia a la grafica de dona
            labelClickeado:'',//label al que se hace click en la grafica de dona
            graficaBarras:'',//variable que guarda referencia a la grafica de barras
            todasListas:[],
            todasCartas:[],
            coloresG2:[], //colores generados para la grafica2
            numeroTotalCartas:[],//numero de cartas para representarlas en la grafica
            cartasListadas:[],//cartas listadas para mostrar en la tabla inferior
            timer: '',
        }
    },
    methods:{
        onSlideStart (slide) {
          this.sliding = true
        },
        onSlideEnd (slide) {
          this.sliding = false
        },
        // llenar arreglo de colores
        llenarColores: function(){
          while (this.colors.length < this.longitudArregloTableros) {
              do {
                  var color = Math.floor((Math.random()*10000000000)+10);
              } while (this.colors.indexOf(color) >= 0);
              this.colors.push("#" + ("000" + color.toString(16)).slice(-6));
          }
        },
        // llenar nombres de tableros y numero de cartas por tablero
        llenarTablerosNumCartas: function(){
          for (let i = 0; i < this.longitudArregloTableros; i++) {
              var nombreTablero=this.tableros[i].TableroNombre;
              var numCartas=this.tableros[i].Numerocarta;
              this.listaTablero.push(nombreTablero);
              this.listaCartas.push(numCartas);
          }
        },
        // grafica la primera grafica que es la grafica de dona
        createChart: function() {
          console.log("create chart");
          var donutChartData = {
            type: 'doughnut',
            data: {
              labels: this.listaTablero,
              datasets: [
                { // one line graph
                  label: 'Total de tareas por Tablero',
                  data: this.numeroTareas,
                  backgroundColor: this.colors,
                  borderWidth: 2
                }
              ]
            },
            options: {
              title: {
                display: true,
                text: 'Total de tareas por Tablero'
              }
            }
          }
          const ctx = document.getElementById('myState');
          const myChart = new Chart(ctx, {
            type: donutChartData.type,
            data: donutChartData.data,
            options: donutChartData.options,
          });
          this.graficaDona = myChart;//obtenemos referencia de la grafica que se lleno
          // console.log(this.graficaDona);
        },
        detectarTablero: function(event){
          var activePoints = this.graficaDona.getElementsAtEvent(event);
          if(activePoints!=undefined){
            var chartData = activePoints[0]['_chart'].config.data;
            var idx = activePoints[0]['_index'];
            this.labelClickeado = chartData.labels[idx];
            this.createChart2(this.labelClickeado);
          }
        },
        // grafica la segunda gráfica y recibe un label como argumento
        createChart2: function(label) {
          if (this.graficaBarras){
            this.graficaBarras.destroy();
            this.todasCartas = [];
            this.numeroTotalCartas = [];
            this.todasListas = [];
          }
          // compara el label del tablero que entra con elementos de la lista de tableros
          for (let i = 0; i < (this.longitudArregloTableros); i++) {
            if (label == this.tableros[i].TableroNombre) {
              var lista = this.tableros[i].Lista;
            }
          }
          for (let i = 0; i < lista.length; i++) {
            var nombreLista = lista[i].NombreLista;
            var numeroCartasporLista = lista[i].cartas.length;
            // console.log(nombreLista);
            this.todasListas.push(nombreLista);
            this.todasCartas.push(numeroCartasporLista);
          }

          this.coloresG2 =[];
          while (this.coloresG2.length < 5000) {
            do {
                var coloresG = Math.floor((Math.random()*10000000000)+10);
            } while (this.coloresG2.indexOf(coloresG) >= 0);
            this.coloresG2.push("#" + ("000" + coloresG.toString(16)).slice(-6));
          }

          for (let index = 0; index < this.todasCartas.length; index++) {
            var element = parseInt(this.todasCartas[index]);
            this.numeroTotalCartas.push(element);
          }

          var barChartData = {
            type: 'bar',
            data: {
                labels: this.todasListas,
                datasets: [{
                    label: label,
                    data: this.numeroTotalCartas,
                    backgroundColor: this.coloresG2,
                    borderWidth: 1
                }]
            },
            options: {
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero:true
                        }
                    }]
                },
              title: {
                display: true,
                text: 'Número de tareas por Lista en el Tablero'
              }
            }
          }
          const ctx = document.getElementById('myPriority');
          const myChart2 = new Chart(ctx, {
            type: barChartData.type,
            data: barChartData.data,
            options: barChartData.options,
          });
          this.graficaBarras = myChart2;
        },

        // genera la lista de las tareas
        elementosLista: function(listaEntrante){
          for (let i = 0; i < (this.longitudArregloTableros); i++) {
            // se compara el label que le hicieron click con el nombre del tablero
            if (this.labelClickeado == this.tableros[i].TableroNombre) {
              // se guarda el arreglo de las listas del tablero
              var lista = this.tableros[i].Lista;
              // se reccorre este arreglo
              for (let index = 0; index < lista.length; index++) {
                // se compara que el nombre de la lista que entra sea igual a una del arreglo
                if(listaEntrante == lista[index].NombreLista){
                  // se llena cartas listadas que son las que se muestran en la tabla inferior
                  this.cartasListadas = lista[index].cartas;
                  console.log(this.cartasListadas);
                }
              }
              
            }
          } 
        }
    },
    mounted () {
      this.createChart();
    },
    created(){
        /*______________________________________________________________________________
        _______________________________________________________________________________-*/
        axios.get('https://cors-anywhere.herokuapp.com/http://geoapps.esri.co/json/pruebaJson.json')
        // axios.get('geoapps.esri.co/json/pruebaJson.json')
        .then(response => {
            // JSON responses are automatically parsed.
            this.tableros = response.data
            this.longitudArregloTableros = this.tableros.length
            this.llenarColores();
            this.llenarTablerosNumCartas();
            //pasar el arreglo de lista de numero de cartas de string a numero, no sirvio el metodo map(Number)
            for (let index = 0; index < this.listaCartas.length; index++) {
              var element = parseInt(this.listaCartas[index]);
              this.numeroTareas.push(element);
            }
        })
        .catch(e => {
            this.errors.push(e)
        })
        // axios para tableros
    }
}
</script>

